	import {
		mapState,
		mapMutations
	} from 'vuex';
	import mInput from '../../components/m-input.vue'
import sunTab from '@/components/sun-tab/sun-tab.vue';
	export default {
		components: {
			sunTab,mInput
		},
		data() {
			return {
				swiperIndex:0,
				index: 0,
				account: '',
				tabSwiperList: ['支付宝','微信',],
			}
		},
		computed: {
			...mapState(['hasLogin', 'forcedLogin'])
		},
		methods: {
			...mapMutations(['logout']),
			bindLogin() {
				uni.navigateTo({
					url: '../login/login',
				});
			},
			bindLogout() {
				this.logout();
				/**
				 * 如果需要强制登录跳转回登录页面
				 */
				if (this.forcedLogin) {
					uni.reLaunch({
						url: '../login/login',
					});
				}
			},
			arrayChange(e){
				console.log('数组数据返回格式');
				console.log(e);
			},
			objectChange(e){
				console.log('对象数据返回格式');
				console.log(e);
			},
			swiperChange(e){
				this.swiperIndex = e.target.current;
			},
			img(img) {
				console.log(img)
				if (img === 0) {
					return '../../static/img/payZFB.jpg';
				}
				else
				{
					return '../../static/img/payWX.jpeg';
				}
			}
		}
	}
